.profileList {
  position:absolute;
  padding:10px;
  top: 20px;
  left: 20px;
  display: flex ;
  flex-direction: column;
  z-index: 2; 
  background-color: rgba( 255, 255, 255, 0.8 );
  border-radius: 10px;
  box-shadow: 2px 2px 2px 2px rgba( 0, 0, 0, 0.1 ) ;
  padding-left:10px;
  margin-right: 20px;
  font-family: 'BMJUA';
  max-height: 300px;
  overflow-y:auto;
  /* border: 1px solid black; */
}

.profileImage {
  /* background-color: blue; */
  border-radius: 50%;
  font-size: 20px;
  width:40px;
  height:40px;
  display: flex;
  text-align: center;
  align-items: center;
  justify-content: center;
  background-color: transparent;
  margin-top: 3px;
  margin-bottom: 3px;
}

.profileImageContent {
  display: flex;
  flex-direction: row;
  justify-content: start;
  align-items: center;
  font-family: 'BMJUA';
  width:100%;
  font-size:20px;
  border: none;
  cursor: pointer;
  background-color: transparent;
  border-radius: 10px;
  background-color: transparent;
  /* -webkit-text-stroke: 0.1px black */
}

.profileImageContent:hover {
  background-color: #D9D9D9;
  border-radius: 10px;
}

.profileContent {
  width:100%;
  margin-left:10px;
  margin-right:10px;
  text-align: center;
}

.outerDiv {
  display: flex;
  flex-direction: column;
}

.taskList {
  position:absolute;
  padding:10px;
  top: 20px;
  right: 20px;
  display: flex ;
  flex-direction: column;
  z-index: 2; 
  background-color: rgba( 255, 255, 255, 0.8 );
  border-radius: 10px;
  box-shadow: 2px 2px 2px 2px rgba( 0, 0, 0, 0.1 ) ;
  padding-left:10px;
  margin-right: 20px;
  font-family: 'BMJUA';
  /* border: 1px solid black; */
}

.taskDiv {
  width:300px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.taskImg {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  border: 5px solid #F5CC1F;
  /* margin: 10px; */
  object-fit: cover;
  object-position: center;
}

.button_default {
  width:40%;
  height:40px;
  background: none;
  background-color: #0F1839;
  border: none;
  font-family: "BMJUA";
  font-size: 20px;
  border-radius: 10px;
  color:white;
  cursor: pointer;
}

.button_default:hover {
  background-color: #F5CC1F;
  color:black;
}

.button_pick {
  width:40%;
  height:40px;
  background: none;
  background-color: #0F1839;
  border: none;
  font-family: "BMJUA";
  font-size: 20px;
  border-radius: 10px;
  color:white;
  background-color: #F5CC1F;
  color:black;
}

.touch:hover {
  background-color: #D9D9D9;
}

.myIcon {
  position:absolute;
  width: 160px;
  top:-50px;
  left:50%;
  transform: translate(-50%, -50%);
  font-size:12px;
  text-align: center;
  color:black;
  background-color: white;
  border : 3px solid #F5CC1F;
  border-radius: 10px;
  box-sizing: border-box;
  padding: 5px;
  padding-top: 10px;
  padding-bottom: 10px;
  color:black;
  font-weight: 300;
  font-family: "BMJUA";
}

.myIconName {
  color:black;
  text-overflow: ellipsis;
  overflow: hidden;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
  word-break: keep-all;
}

.toggle {
  position: absolute;
  top:20px;
  right:365px;
  z-index: 2;
  padding:10px;
  display: flex ;
  flex-direction: column;
  background-color: rgba( 255, 255, 255, 0.8 );
  border-radius: 10px;
  box-shadow: 2px 2px 2px 2px rgba( 0, 0, 0, 0.1 ) ;
  cursor: pointer;
}

.focus_on {
  width: 30px;
  height:30px;
  cursor: pointer;
}

.focus_off {
  width: 30px;
  height:30px;
  cursor: pointer;
}

.toggle2 {
  position: absolute;
  top:20px;
  right:50%;
  z-index: 2;
  padding:10px;
  display: flex ;
  flex-direction: column;
  background-color: rgba( 255, 255, 255, 0.8 );
  border-radius: 10px;
  box-shadow: 2px 2px 2px 2px rgba( 0, 0, 0, 0.1 ) ;
  cursor: pointer;
  width:30px;
  height:30px;
}

.toggle3 {
  position: absolute;
  top:20px;
  right:45%;
  z-index: 2;
  padding:10px;
  display: flex ;
  flex-direction: column;
  background-color: rgba( 255, 255, 255, 0.8 );
  border-radius: 10px;
  box-shadow: 2px 2px 2px 2px rgba( 0, 0, 0, 0.1 ) ;
  cursor: pointer;
  width:30px;
  height:30px;
  overflow: hidden;
}

.refresh_on {
  width: 30px;
  height:30px;
  cursor: pointer;
}

.refresh_off {
  width: 30px;
  height:30px;
  cursor: pointer;
}

.refreshToggle {
  position: absolute;
  top:75px;
  right:49.5%;
  z-index: 2;
  text-align: center;
  transform: translate(-15%, 0%);
  font-family: "BMJUA";
  color:#0F1839;
}

.autoRefresh {
  width: 50px;
  height:50px;
  cursor: pointer;
  transform: translate(-17%, -22%);
  top:-10px;
}

.autoRefreshMent {
  position: absolute;
  top:75px;
  right:45%;
  z-index: 2;
  text-align: center;
  transform: translate(-15%, 0%);
  font-family: "BMJUA";
  color:#0F1839;
}